import React, { useState, useEffect, useMemo, useCallback } from 'react';
import {
    Container,
    Typography,
    Box,
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Chip,
    CircularProgress,
    Alert,
    Button,
    Pagination,
    TextField,
    InputAdornment,
    IconButton,
    Tooltip,
} from '@mui/material';
import {
    Search as SearchIcon,
    Visibility as VisibilityIcon,
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { format } from 'date-fns';
import { createAuthAxios } from '../../utils/auth';

const ITEMS_PER_PAGE = 10;

// 
const API_URL = process.env.REACT_APP_ORDER_SERVICE_URL;

const Orders = () => {
    const navigate = useNavigate();
    const [allOrders, setAllOrders] = useState([]); // 
    const [displayedOrders, setDisplayedOrders] = useState([]); // 
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');
    const [page, setPage] = useState(1);
    const [searchInput, setSearchInput] = useState('');

    const authAxios = useMemo(() => createAuthAxios(navigate), [navigate]);

    // 
    const fetchOrders = useCallback(async () => {
        setLoading(true);
        try {
            const params = new URLSearchParams();
            params.set('page', '1'); // 
            params.set('limit', '100'); // 

            //  API 
            const response = await authAxios.get(`${API_URL}/api/v1/orders/?${params.toString()}`);

            console.log('API:', response.data);

            if (response.data && Array.isArray(response.data.orders)) {
                setAllOrders(response.data.orders);
                setError('');
            } else {
                setError('');
                setAllOrders([]);
            }
        } catch (error) {
            console.error(':', error);
            setError(error.response?.data?.message || '');
            setAllOrders([]);
        } finally {
            setLoading(false);
        }
    }, [authAxios]);

    // 
    useEffect(() => {
        fetchOrders();
    }, [fetchOrders]);

    // 
    useEffect(() => {
        if (!searchInput.trim()) {
            // 
            const startIndex = (page - 1) * ITEMS_PER_PAGE;
            const endIndex = startIndex + ITEMS_PER_PAGE;
            setDisplayedOrders(allOrders.slice(startIndex, endIndex));
            return;
        }

        // 
        const searchTerm = searchInput.toLowerCase();

        // 
        const filteredOrders = allOrders.filter(order => {
            // 
            const orderNumber = getOrderNumber(order).toLowerCase();
            if (orderNumber.includes(searchTerm)) return true;

            // 
            if (order.items && order.items.length > 0) {
                return order.items.some(item =>
                    (item.name || '').toLowerCase().includes(searchTerm)
                );
            }

            return false;
        });

        // 
        const startIndex = (page - 1) * ITEMS_PER_PAGE;
        const endIndex = startIndex + ITEMS_PER_PAGE;
        setDisplayedOrders(filteredOrders.slice(startIndex, endIndex));
    }, [allOrders, searchInput, page]);

    // 
    const handlePageChange = (event, value) => {
        setPage(value);
        window.scrollTo(0, 0);
    };

    // 
    const handleSearchInputChange = (e) => {
        setSearchInput(e.target.value);
        setPage(1); // 
    };

    const getStatusColor = (status) => {
        switch (status) {
            case 'pending':
                return 'warning';
            case 'processing':
                return 'info';
            case 'shipped':
                return 'primary';
            case 'delivered':
                return 'success';
            case 'cancelled':
                return 'error';
            default:
                return 'default';
        }
    };

    const getStatusText = (status) => {
        switch (status) {
            case 'pending':
                return '';
            case 'processing':
                return '';
            case 'shipped':
                return '';
            case 'delivered':
                return '';
            case 'cancelled':
                return '';
            default:
                return status;
        }
    };

    const formatDate = (dateString) => {
        if (!dateString) return '';
        try {
            return format(new Date(dateString), 'yyyy/MM/dd HH:mm');
        } catch (error) {
            console.error(':', error);
            return dateString;
        }
    };

    //  (APIorderNumber)
    const getOrderNumber = (order) => {
        if (order.orderNumber) return order.orderNumber;

        // ID8
        const shortId = order.id.substring(0, 8).toUpperCase();
        return `ORD-${shortId}`;
    };

    // 
    const getOrderItemsSummary = (order) => {
        if (!order.items || order.items.length === 0) {
            return '';
        }

        // 
        const itemsWithName = order.items.filter(item => item.name);

        if (itemsWithName.length === 0) {
            // 
            return `${order.items.length} `;
        }

        if (itemsWithName.length === 1) {
            // 
            const item = itemsWithName[0];
            return `${item.name} x ${item.quantity}`;
        }

        // 
        return `${itemsWithName[0].name}  ${itemsWithName.length} `;
    };

    // 
    const filteredTotalPages = useMemo(() => {
        if (!searchInput.trim()) {
            return Math.ceil(allOrders.length / ITEMS_PER_PAGE);
        }

        const searchTerm = searchInput.toLowerCase();
        const filteredCount = allOrders.filter(order => {
            const orderNumber = getOrderNumber(order).toLowerCase();
            if (orderNumber.includes(searchTerm)) return true;

            if (order.items && order.items.length > 0) {
                return order.items.some(item =>
                    (item.name || '').toLowerCase().includes(searchTerm)
                );
            }

            return false;
        }).length;

        return Math.ceil(filteredCount / ITEMS_PER_PAGE);
    }, [allOrders, searchInput]);

    return (
        <Container maxWidth="lg" sx={{ py: 4 }}>
            <Typography variant="h4" component="h1" gutterBottom>
                
            </Typography>

            <Paper sx={{ p: 2, mb: 3 }}>
                <Box sx={{ mb: 2, display: 'flex' }}>
                    <TextField
                        fullWidth
                        size="small"
                        placeholder=""
                        value={searchInput}
                        onChange={handleSearchInputChange}
                        InputProps={{
                            endAdornment: (
                                <InputAdornment position="end">
                                    <IconButton edge="end">
                                        <SearchIcon />
                                    </IconButton>
                                </InputAdornment>
                            ),
                        }}
                    />
                </Box>
            </Paper>

            {error && (
                <Alert severity="error" sx={{ mb: 2 }}>
                    {error}
                </Alert>
            )}

            {loading ? (
                <Box display="flex" justifyContent="center" py={4}>
                    <CircularProgress />
                </Box>
            ) : displayedOrders.length === 0 ? (
                <Alert severity="info">
                    {searchInput.trim() ? '' : ''}
                </Alert>
            ) : (
                <TableContainer component={Paper}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell align="right"></TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {displayedOrders.map((order) => (
                                <TableRow key={order.id}>
                                    <TableCell component="th" scope="row">
                                        {getOrderNumber(order)}
                                    </TableCell>
                                    <TableCell>
                                        <Tooltip title={order.items?.map(item => `${item.name || ''} x ${item.quantity}`).join(', ') || ''}>
                                            <Typography noWrap sx={{ maxWidth: 200 }}>
                                                {getOrderItemsSummary(order)}
                                            </Typography>
                                        </Tooltip>
                                    </TableCell>
                                    <TableCell>{formatDate(order.createdAt)}</TableCell>
                                    <TableCell>NT$ {order.totalAmount.toLocaleString()}</TableCell>
                                    <TableCell>
                                        <Chip
                                            label={getStatusText(order.status)}
                                            color={getStatusColor(order.status)}
                                            size="small"
                                        />
                                    </TableCell>
                                    <TableCell align="right">
                                        <Button
                                            startIcon={<VisibilityIcon />}
                                            size="small"
                                            onClick={() => navigate(`/orders/${order.id}`)}
                                        >
                                            
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            )}

            {filteredTotalPages > 1 && (
                <Box sx={{ mt: 4, display: 'flex', justifyContent: 'center' }}>
                    <Pagination
                        count={filteredTotalPages}
                        page={page}
                        onChange={handlePageChange}
                        color="primary"
                    />
                </Box>
            )}
        </Container>
    );
};

export default Orders; 